    export default {
    name: 'lists',
    data(){
        return {
            items:[]
        }
    },
    mounted(){
        this.getInfo();
    },
    methods:{
        getInfo(){
            let _this = this;
            this.post({
                url: "/fundRank/fundRankList/v1.0",
                success: function(e) {
                    if (e.code == "0000") {
                        _this.items=e.result.list;
                    }
                }
            })
        }
    }
}



